// src/components/QualifiersInfo.js
import React from 'react';
import { Typography, Container, Paper } from '@mui/material';
import LocalPlayIcon from '@mui/icons-material/LocalPlay'; // Substitua pelo cone desejado
import PeopleIcon from '@mui/icons-material/People'; // Substitua pelo cone desejado
import SportsEsportsIcon from '@mui/icons-material/SportsEsports'; // Substitua pelo cone desejado
import image from './background_mobile.png'

function QualifiersInfoMobile() {
  return (
    <section className='QualifiersInfoMobile'>
      <div>
        <img className='background_mobile' src={image}></img>
      </div>
      <div>
        <div elevation={3} className="informativo_mobile" style={{ padding: '20px', background: 'rgba(59, 59, 59, 0.7)' }}>
          <div className='info-divide'>
            <Typography variant="h5"><LocalPlayIcon fontSize="small" style={{ marginRight: '10px' }} />CCG Qualifiers</Typography>
            <Typography variant="body1" style={{ marginTop: '10px'}}>
              CCG qualifiers, so torneios classificatrios, executados por lojas WPN de todo o Brasil, que qualificam jogadores para vagas da CCG Showdown.
            </Typography>
          </div>
          <div className='info-divide'>
            <Typography variant="h5"><PeopleIcon fontSize="small" style={{ marginRight: '10px' }} />Quem pode jogar os qualificatrios?</Typography>
            <Typography variant="body1" style={{ marginTop: '10px'}}>
              Qualquer pessoa poder participar dos torneios de qualificao, contanto que possua uma wizards account.
            </Typography>
          </div>
          <div className='info-divide'>
            <Typography variant="h5"><SportsEsportsIcon fontSize="small" style={{ marginRight: '10px'}} />Como me qualifico para o CCG showdown?</Typography>
            <Typography variant="body1" style={{ marginTop: '10px'}}>
            O vencedor de cada uma das etapas realziadas em lojas WPN, estar qualificado e poder inscrever-se no torneio CCG showdown do ciclo correspondente  sua qualificao.
            </Typography>
          </div>
        </div>
      </div>
    </section>
  );
}

export default QualifiersInfoMobile;
